video{
    object-fit:cover;
    
    height:100%;
    width:100%;
    position: fixed;
    z-index: -1;
    max-width:1500px;
}

.intro{
    color: #fff;
    margin: 5px 6px;
    font-size: 16px;
    font-weight: bold;
}

.intro1{
    color: #fff;
    margin: 5px 6px;
    font-size: 30px;
    font-weight: bold;
}

.me_link{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.me_image{
    width: 200px;
    height: 50%;
    object-fit:contain;
    border-radius: 50%;
  }

@media screen and (max-width:700px) {
    video{
        object-fit:cover;
        height:100%;
        width:603px;
        position: fixed;
        z-index: -1;
        max-width:1500px;
    }
    .my-text{
        font-size:14px;
    }
    

    .hero-container >p{
        font-size: 10px;
    }
  }
  

.hero-container{
     background-image: url('./myback.jpg');
     height: 100vh;
     width: 100%;
     justify-content: center;
     display: flex;
     flex-direction:column;
     align-items: center;
     padding-top: 30px;
     box-shadow: inset 0 0 0 1000px rgba(0, 0, 0,0.2);
     
}

.hero-container >h1{
    color: #fff;
    font-size: 80px;
   
}

.hero-container >p{
    margin-top:8px;
    color: #fff;
    margin-bottom: 10px;
    font-size: 32px;
}

.hero-btns{
    margin-top:32px;
}

.hero-btns .btn{
    margin:8px;
}



/* This Took Carew of Mobile Well */
@media screen and (max-width:1080px){
    .hero-container >h1{
        color: #fff;
        font-size: 30px;

    }
   
    .hero-container >p{
      font-size:15px
    }
}


/* This Took Carew of Mobile Well */
@media screen and (max-width:780px){
    .hero-container >h1{
        color: #fff;
        font-size: 30px;
        
    }

    .btn-mobile{
        display: block;
        text-decoration:none;
    }

    .btn{
        width:100%;
    }

    .hero-container >p{
        font-size:15px
      }
}
