.fa-times{
    color: var(--text);
    font-size:2rem;
}

.nav-links-mobile{
    display:block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    color:var(--text);
    padding: 14px 20px;
    font-size: 1.5rem;
    border:1px solid #fff;
    background-color: brown;
    text-decoration: none;
    transition: all 0.3s ease-out;
}

.nav-links-mobile:hover{
    background: #fff;
    color: #242424;
    transition: 1150ms;
}

.nav-links {
    color:var(--text);
    display:flex;
    align-items:center;
    text-decoration:none;
    padding: 0.5rem 1rem;
    height: 100%;
}


.nav-links:hover{
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
}


.fa-bars{
    color:var(--text);
    font-size: 3rem;
  }
  

:root{
    --text:#fff;
}


.nav-links-mobile{
    display:none;
}


.menu-icon{
    display:none;
}

.navbar{
    background:linear-gradient(90deg , rgb(28,27,27)0% , rgb(26,23,23)100%);
    height: 100px;
    display:flex;
    width: 100%;
    justify-content:center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top:0;
    z-index:999;
}

.navbar-logo{
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor:pointer;
  text-decoration:none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}


.nav-item{
    height:80px;
}

.navbar-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;
 } 

 
.nav-menu{
    display:grid;
    grid-template-columns:repeat(4,auto);
    grid-gap:10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    margin-right: 2rem;
    justify-content:end;
 }

 @media screen and (max-width:960px){
     .NavBarItems{
          position:relative;
     }

     .nav-menu{
         display: flex;
         flex-direction: column;
         width:80%;
         height:90vh;
         position: absolute;   
         top: 80px;  
         left: -100%;
         opacity:1;
         transition: all 0.5s ease;
     }

     .nav-menu.active{
         background: #242222;
         left:0;
         opacity:1;
         transition: all 0.5s ease;
         z-index:1;
     }

     .nav-links{
         text-align: center;
         padding: 2rem;
         width: 100%;
         display: table;
     }

     .nav-links:hover{
         background-color: #fff;
         color: #242424;
         border-radius: 0;
     }

     .navbar-logo{
       position: absolute;
       top: 0;
       left: 0;
        transform:translate(25% , 50%);
     }
    
     .menu-icon{
         display: block;
         top: 0;
         right: 0;
         transform: translate(-100%,60%);
         font-size: 1.8rem;
         position:absolute;
     }
     .fa-times{
         color: #fff;
         font-size: 2rem;
     }

     .nav-links-mobile{
         display: block;
         text-align: center;
         margin: 2rem auto;
         border-radius: 4px;
         width: 80%;
         text-decoration:none;
         font-size: 1.5rem;
         background-color:transparent;
         color: #fff;
         padding:14px 20px;
         border:1px solid #fff;
         transition: all 0.3s ease-out;
     }

     .nav-links-mobile:hover{
         background: #fff;
         color: #242424;
         transition: 250ms;
     }
 }