:root{
    --primary:#fff;
}

.btn{
    padding: 8px 20px;
    border-radius:2px;
    cursor: pointer;
    color:var(--primary);
    border: none;
    outline: none;
}

.btn--primary{
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}


.btn--outline{
    background-color: transparent;
    color:#fff;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    border: 1px solid var(--primary);
}

.btn--medium{
   padding: 8px 20px;
   font-size:20px;
}

.btn--large{
  padding: 12px 26px;
  font-size: 20px;
}

.btn--medium:hover , .btn--large:hover{
    background: #fff;
    color: blueviolet;
    transition:  all 0.3s ease-out;
}